<template>
	<div class="good-container">
		<transition :name="'slide-' + (is_active ? 'in' : 'out')">
			<router-view></router-view>
		</transition>
		<span class="cart-btn cursor" :class="is_active?'active':''" @click="showCart">
			<Icon :type="is_active?'chevron-right':'chevron-left'"></Icon>
		</span>
	</div>
</template>

<script>
	export default{
		data () {
			return {
				is_active: false
			}
		},
		methods: {
			showCart () {
				this.is_active = !this.is_active
				let map_uri = {true: './cart',false: './index'}
				this.$router.push(map_uri[this.is_active])
			}
		}
	}
</script>

<style lang="less">
	.slide-in-enter-active,
	.slide-in-leave-active,
	.slide-out-enter-active,
	.slide-out-leave-active {
	  will-change: transform;
	  transition: all .5s;
	  width: 100%;
	  height: 100%;
	  position: absolute;
	  backface-visibility: hidden;
	  perspective: 1000;	  
	}		
	.slide-out-enter {
	  opacity: 0;
	  transform: translate3d(-100%, 0, 0);
	}
	.slide-out-leave-active {
	  opacity: 0;
	  transform: translate3d(100%, 0, 0);
	}
	.slide-in-enter {
	  opacity: 0;
	  transform: translate3d(100%, 0, 0);
	}
	.slide-in-leave-active {
	  opacity: 0;
	  transform: translate3d(-100%, 0, 0);
	}
</style>


